export default {
  name: 'MusicPlayer',
  props: {
    singerName: {
      type: String,
      default: ''
    },
    songName: {
      type: String,
      default: ''
    },
    singerPic: {
      type: String,
      default: ''
    },
    src: {
      type: String,
      default: ''
    },
    autoplay: {
      type: Boolean,
      default: false
    },
    mobile: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      volume: 100,
      muted: false,
      playing: this.autoplay,
      sliderTime: 0,
      maxTime: 0,
      mousedown: false,
      repeat: true
    }
  },
  computed: {
    icon() {
      const val = this.volume
      if (this.muted || val <= 0) {
        return 'mdi-volume-mute'
      } else if (val <= 35) {
        return 'mdi-volume-low'
      } else if (val > 70) {
        return 'mdi-volume-high'
      } else {
        return 'mdi-volume-medium'
      }
    },
    playIcon() {
      return this.playing ? 'mdi-pause' : 'mdi-play'
    },
    repeatIcon() {
      return this.repeat ? 'mdi-repeat' : 'mdi-repeat-off'
    }
  },
  watch: {
    volume(val) {
      this.muted = false
      let vol = val
      if (val < 0) {
        vol = 0
      } else if (val > 100) {
        vol = 100
      }
      this.$refs.audio.volume = vol / 100
      localStorage.setItem('audioVolume', vol)
      return vol
    },
    muted(val) {
      localStorage.setItem('audioMuted', val)
      this.$refs.audio.muted = val
    }
  },
  created() {
  },
  mounted() {
    const vol = localStorage.getItem('audioVolume')
    if (vol != null && vol !== '' && !isNaN(vol)) {
      this.volume = parseInt(vol)
    }
    this.$nextTick(() => {
      const muted = localStorage.getItem('audioMuted')
      this.muted = muted === 'true'
    })
  },
  methods: {
    scrollBarWheel(e) {
      e = e || window.event
      let down = true // 定义一个标志，当滚轮向下滚时，执行一些操作
      down = e.wheelDelta ? e.wheelDelta < 0 : e.detail > 0
      if (down) {
        this.volume -= 5
      } else {
        this.volume += 5
      }
    },
    paly() {
      if (this.playing) {
        this.$refs.audio.pause()
      } else {
        this.$refs.audio.play()
      }
    },
    onPlay(e) {
      this.playing = true
      // this.$refs.audio.volume = this.volume / 100
      // this.$refs.audio.muted = this.muted
      // console.log('this.muted', this.muted)
      // console.log(e)
    },
    onPause(e) {
      // console.log(e)
      this.playing = false
    },
    OnEnded(e) {
      // console.log(e)
      this.playing = false
    },
    onError(e) {
      console.log(e)
    },
    onWaiting(e) {
      // console.log(e)
    },
    onTimeupdate(e) {
      if (this.mousedown) {
        return
      }
      this.sliderTime = parseInt(e.target.currentTime)
    },
    onLoadedmetadata(e) {
      // console.log('onLoadedmetadata', e)
      this.maxTime = parseInt(e.target.duration)
      this.sliderTime = parseInt(e.target.currentTime)
    },
    fmtTime(time) {
      return `${parseInt(time / 60)}:` + ('' + (time % 60)).padStart(2, 0)
    },
    setTime() {
      this.mousedown = false
      // console.log(this.$refs.audio)
      this.$refs.audio.currentTime = this.sliderTime
    }
  }
}
