import QRCode from 'qrcode'
import MusicPlayer from '~/components/MusicPlayer'
import { deviceType } from '~/utils/deviceType'
export default {
  layout: 'none',
  components: {
    MusicPlayer
  },
  validate({ params }) {
    // 必须是number类型
    return /^\d+$/.test(params.id)
  },
  asyncData({ params, $axios, req, redirect }) {
    let userAgent
    if (process.server) {
      userAgent = req.headers['user-agent']
    } else {
      userAgent = navigator.userAgent
    }
    const dt = deviceType(userAgent)
    const mobile = dt.type !== 'pc'
    if (!mobile) {
      redirect(`/song/${params.id}`)
    }
    return $axios
      .post(`/getSongDetail`, {
        songId: parseInt(params.id)
      })
      .then((data) => {
        data.lyrics = data.lyric.split('\n')
        data.mobile = mobile
        data.dt = dt
        return data
      })
  },
  data() {
    return {
      listenUrl: undefined,
      mobile: false,
      appUrl: '下载地址',
      appQR: ''
    }
  },
  computed: {
    singerImg() {
      const names = (this.singerName || '').split('、')
      // if (this.singerName.includes('、')) {
      //   return `/music/pic/固定2.jpg`
      // }
      // return `/music/pic/${this.singerName}2.jpg`
      if (names.length > 2 || names.length === 0) {
        return 'https://cdn.jsdelivr.net/gh/qianxun666/singer-img@master/固定2.jpg'
      }
      return `https://cdn.jsdelivr.net/gh/qianxun666/singer-img@master/${names[0].trim()}2.jpg`
    }
  },
  created() {
  },
  mounted() {
    this.calcAppQR()
  },
  methods: {
    calcAppQR() {
      if (!this.appUrl) {
        return
      }
      QRCode.toDataURL(this.appUrl)
        .then((url) => {
          this.appQR = url
        })
        .catch((err) => {
          console.error(err)
        })
    }
  }
}
